//   3  :
// 1., 2., 3.;
//  5 :
// 1. , 2.hover, 3.focus, 4.click(active), 5.disabled
.button {
  // c  ('o)
  display: inline-block;
  padding: 0;
  margin: 0;
  border: none;
  cursor: pointer;

  //
  min-width: 197px;
  height: 52px;
  padding: 0 30px;
  border-radius: 4px;

  font-family: inherit;
  @include font(400, 18px, calc(22px / 18px));
  text-align: center;

  // :
  &.dark {
    color: var(--dark-theme-text-color);
    background-color: var(--main-bgc-accent);
  }
  //  :
  &.light {
    color: var(--main-accent-color);
    background-color: transparent;
    border: 1px solid var(--main-accent-color);
  }

  transition: color var(--main-transition),
    background-color var(--main-transition), border var(--main-transition);

  &:hover,
  &:focus {
    color: var(--dark-theme-text-color);
    background-color: var(--green-light);
    border: none;
  }
  &:active {
    color: var(--dark-theme-text-color);
    background-color: var(--main-accent-color);
    border: none;
  }

  &.disabled {
    color: #c8c8c8;
    background-color: #dedede;
    border: none;
  }
  //  
  &.header-button {
    min-width: 166px;
    height: 32px;

    @include font(700, 10px, calc(12px / 10px));

    @include desktop {
      height: 37px;
      @include font(null, 14px, calc(17px / 14px));
    }

    color: var(--dark-theme-text-color);
    background-color: #b9956f;

    &:hover,
    &:focus {
      background-color: #d2a97e;
    }
    &:active {
      background-color: var(--second-accent-color);
    }
    &.disabled {
      background-color: #f3dcc3;
    }
  }
}
//     '-->'
.button-icon {
  display: inline-block;
  margin-left: 10px;

  width: 32px;
  height: 12px;

  //    :
  // svg {
  //     fill: currentColor;
  // }
}

.header-button {
  min-width: 166px;
  height: 32px;

  @include font(700, 10px, calc(12px / 10px));

  @include desktop {
    height: 37px;
    @include font(null, 14px, calc(17px / 14px));
  }

  color: var(--dark-theme-text-color);
  background-color: #b9956f;

  &:hover,
  &:focus {
    background-color: #d2a97e;
  }
  &:active {
    background-color: var(--second-accent-color);
  }
  &.disabled {
    background-color: #f3dcc3;
  }
}
